:global(#menuButton) {
    width: 100%;
    font-size: 2.8vmin;
    padding: .2vmax;
    border-radius: .28571429rem .28571429rem 0 0;
    color: rgb(102,137,202);
    box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.6)!important;
    background-color: whitesmoke;
    text-shadow: -2px 0 0 black, 0 2px 0 black, 0 -2px 0 black, 1px 1px black, -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black!important;
    i {
        margin:0;
    }
    .menu {
        margin: 0;
    }
}