.app-wrapper {
  display: flex;
  flex-direction: column;
  overflow: auto;
  margin: 0 auto;
  flex: 1;
  max-height: max-content;
}

.header-with-button {
  position: fixed;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  top: 0;
  left:0;
  z-index: 500;
  height: 10vh;
  min-width: 100%;
  background-color: rgb(102, 137, 202);
  border-width: 0.2em 0.2em 0em;
  border-style: groove;
  border-color:  rgb(66, 37, 172);
}

.app-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(10vh - .2em);
}

.menu-with-content{
  display: flex;
  flex: 1;
  width: 100vw;
  padding-top: 10vh;
}

.menu-button {
  align-self: flex-start;
  padding-bottom: 2px;
  position: absolute;
  bottom: 0;
}

.app-menu.disabled {
  display: flex;
  flex: 0;
  height: 0;
  transition: flex 0.3s ease-out;
}

.app-wrapper-content {
  display: flex;
  flex: 1 1 80vw;
  max-width: 100vw;
  height: 100%;
  justify-content: center;
  background-color: rgb(213, 219, 224);
  border-width: 0 0.2em 0.2em 0;
  border-style: groove;
  border-color: rgb(66, 37, 172);
}