:global(#to-top){


  .buttonToTop {
    display: flex;
    flex-direction: column;
    position: fixed;
    justify-content: center;
    align-items: center;
    width: 4vh;
    height: 4vh;
    bottom: 3vh;
    right: 3vw;
    padding: 0;
    border-radius: 30%;
    background-color: rgb(102, 137, 202);
   }

    .buttonToTop:hover {
        color: rgb(102, 137, 202);
        background-color: white;
        border: 2px solid black;
    }

    .chevron {
        margin-top: 10px;
        animation: scrollTop .5s alternate cubic-bezier(0.4, 0, 1, 1) infinite;
    }

    @keyframes scrollTop {
        from{
            transform: translateY(0vh);
        }
        to {
            transform: translateY(-1.5vh);
        }
    }
}