:global(#header) {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 55%;
  max-height: 4vh;
  padding: 1vmin;
  
  .image {
    max-height: 4vmin;
    max-width: 4vmax;
  }

  .login {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 35vmax;
    padding-right: 3vw;;
    font-size: 2vmax;
  }

  .buttonLog {
    display: flex;
    font-size: 1vmax;
    padding: 0.7em;
    background-color: rgb(150, 144, 144);
    
  }

  .buttonLog:hover {
    border-radius: .28571429rem;
    background-color: white;
    box-shadow:  inset 0px 0px 2px 2px rgba(0,0,0,0.6)!important;
  }

  .buttonLog:active {
    border-radius: .28571429rem;
    background-color: white;
    box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.6)!important;
  }

  .comment {
    margin: 0px 5px 0px;
  }

  .newComment {
    margin: 0px 5px 0px;
    color: rgb(218, 24, 24)
  }
}