  
  :global(#not-visible) {
    display: flex;
    flex: 0 1 0;
    top: 0;
    left: 0;
    margin: 0;
    border: 0;
    transition: flex .8s ease;
    border-radius: 0 0.28rem .28rem 0;
    background-color: rgb(213, 219, 224);
  }

  :global(#visible){
    display: flex;
    flex: 1 1 20vw;
    max-width: 20vw;
    top: 0;
    left: 0;
    margin: 0;
    border: 0;
    transition: flex .8s ease;
    border-radius: 0 0.28rem .28rem 0;
    background-color: rgb(213, 219, 224);
  }

    :global(#columnWithButtons) {
      min-width: 100%;
      position: relative;
      visibility: inherit;
      transform: translate3d(0,0,0);
      left: 0;
      top: 0;
      border: 0;
      background-color: rgb(102, 137, 202);


 .item{
      padding: 0;
      color: rgb(219, 223, 230);
      text-decoration: none;
  }

  .notActive {
    display:flex;
    flex: 1;
    max-width: 20vw;
    height: 5vh;
    text-align: left;
    align-items: flex-end;
    font-size: 2.5vmin;
    padding: .5vmin 0 .5vmin 2vmin;
    color: white;
    background-color: rgb(102, 137, 202);
  }

  .notActive:hover {
    box-shadow: 4px 15px 100px 38px rgba(34, 60, 80, 1) inset;
  }

  .notActive.activeItem {
    height: 8vh;
    justify-content:  center;
    align-items: center;
    color: rgb(102, 137, 202);
    background-color: rgb(213, 219, 224);
    text-decoration: underline;
  }

  .notActive.activeItem:hover {
    color: rgb(213, 219, 224);
  }

  
  .notActive.activeItem::after {
    content: "";
    position: absolute;
    top: 10%;
    left: 5%;
    right: 4%;
    bottom: 10%;
    border-top: .11em solid rgb(31, 99, 145) ;
    border-bottom: .11em  solid rgb(31, 99, 145);
    border-left: .11em solid rgb(31, 99, 145);
  }



  }